import uniIcon from '@/components/uni-icon/uni-icon.vue';
import { post } from '@/api/user.js';
import { api } from '@/config/common.js';
export default {
	data() {
		return {
			loading: false,
			stop: false,
			miao: 60,
			loginname: '',
			mobile: '',
			code: '',
			status: 0,
			password: '',
			new_password: '',
			re_new_password: '',
			title: '登录'
		};
	},
	components: { uniIcon },
	onLoad() {
		// 	uni.login({
		// 	  provider: 'weixin',
		// 	  success: function (loginRes) {
		// 	    console.log(loginRes);
		// 	  }
		// 	});
		// uni.getUserInfo({
		// 				provider: 'weixin',
		// 				success: function(infoRes) {
		// 					console.log(infoRes);
		// 				}
		// 			});
	},
	onShow() {},
	methods: {
		handleNavbarClickLeft() {
			uni.switchTab({
				url: '/pages/my/my'
			});
		},
		reg_action() {
			uni.reLaunch({
				url: '/pages/my/login/reg'
			});
		},
		forget_action() {
			uni.reLaunch({
				url: '/pages/my/login/forget'
			});
		},
		handleWxLogin(result) {
			console.log(result);
		},

		handleLogin() {
			const { loginname, password } = this;
			if (loginname.length == 0) {
				this.$api.msg('登录账号不能为空！');
				return;
			}
			if (password.length == 0) {
				this.$api.msg('登录密码不能为空！');
				return;
			}
			const sendData = {
				loginname,
				password
			};
			this.loading = true;
			post(api.login, sendData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						let userInfo = {
							token: res.data.data.token,
							exp: res.data.data.exp,
							userId: res.data.data.userId
						};
						uni.setStorage({
							key: 'userInfo',
							data: userInfo,
							success: function() {
								uni.switchTab({
									url: '/pages/index/index'
								});
							}
						});
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		}
	}
};
